const BASE_PATH = (import.meta.env.BASE_URL || '').replace(/\/$/, '');

const CSRF_TOKEN_KEY = 'eda_csrf_token';

function getOrCreateCsrfToken(): string {
  let token = localStorage.getItem(CSRF_TOKEN_KEY);
  if (!token) {
    token = crypto.randomUUID();
    localStorage.setItem(CSRF_TOKEN_KEY, token);
  }
  return token;
}

export type BlogPost = {
  id: number;
  title: string;
  excerpt: string;
  image: string;
  author: string;
  date: string;
  readTime: string;
  category: string;
};

export async function fetchBlogPosts(limit = 6): Promise<BlogPost[]> {
  // Example dynamic content using public API
  const res = await fetch('https://jsonplaceholder.typicode.com/posts?_limit=' + limit, {
    headers: { 'Accept': 'application/json' },
  });
  const posts = await res.json();
  const mapped: BlogPost[] = posts.map((p: any, idx: number) => ({
    id: p.id,
    title: p.title.charAt(0).toUpperCase() + p.title.slice(1),
    excerpt: p.body,
    image: `https://picsum.photos/seed/blog-${p.id}/600/400`,
    author: ['Academy Team', 'Col. Rajesh', 'Dr. Amit'][idx % 3],
    date: new Date().toLocaleDateString(),
    readTime: `${4 + (idx % 6)} min read`,
    category: ['Exam Updates', 'Study Tips', 'SSB'][idx % 3],
  }));
  return mapped;
}

export type Notice = {
  id: number;
  title: string;
  message: string;
  urgent: boolean;
  date: string;
};

export async function fetchNotices(): Promise<Notice[]> {
  // Prefer local JSON so it works offline as well
  const res = await fetch(`${BASE_PATH}/data/notices.json`, { cache: 'no-cache' });
  return res.json();
}

export async function submitContactForm(payload: Record<string, string>) {
  const token = getOrCreateCsrfToken();
  const res = await fetch('https://httpbin.org/post', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      'X-CSRF-Token': token,
    },
    body: JSON.stringify(payload),
  });
  if (!res.ok) throw new Error('Failed to submit');
  return res.json();
}

export { getOrCreateCsrfToken };