import React, { useEffect, useMemo, useRef, useState } from 'react';
import { MessageCircle, X, Send } from 'lucide-react';
import DOMPurify from 'dompurify';

type ChatMessage = {
  id: string;
  role: 'user' | 'assistant';
  content: string;
};

const defaultSuggestions = [
  'What courses do you offer?',
  'How do I prepare for NDA?',
  'Do you provide hostel facilities?',
];

const Chatbot: React.FC = () => {
  const [open, setOpen] = useState(false);
  const [input, setInput] = useState('');
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [loading, setLoading] = useState(false);
  const endRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    endRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages, open]);

  const placeholderEndpoint = import.meta.env.VITE_CHATBOT_ENDPOINT;

  const sanitizedInput = useMemo(() => DOMPurify.sanitize(input), [input]);

  const sendMessage = async (content?: string) => {
    const text = (content ?? sanitizedInput).trim();
    if (!text) return;
    const userMsg: ChatMessage = { id: crypto.randomUUID(), role: 'user', content: text };
    setMessages((prev) => [...prev, userMsg]);
    setInput('');
    setLoading(true);

    try {
      let replyText = '';
      if (placeholderEndpoint) {
        const res = await fetch(placeholderEndpoint, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ messages: [{ role: 'user', content: text }] }),
        });
        const data = await res.json();
        replyText = data?.reply || 'Thanks for your question! Our team will get back to you shortly.';
      } else {
        // Fallback heuristic response
        const lower = text.toLowerCase();
        if (lower.includes('course') || lower.includes('fees')) {
          replyText = 'We offer NDA, CDS, AFCAT, and SSB programs. Fees vary by course; please see the Courses page for details.';
        } else if (lower.includes('admission') || lower.includes('enroll')) {
          replyText = 'Admissions are open year-round for select programs. Tap Enroll Now on the homepage or contact us for guidance.';
        } else if (lower.includes('hostel') || lower.includes('accommodation')) {
          replyText = 'Yes, limited hostel facilities are available. Please contact our office for availability and details.';
        } else {
          replyText = "I'm here to help with courses, admissions, and preparation tips. Ask me anything!";
        }
      }
      const botMsg: ChatMessage = { id: crypto.randomUUID(), role: 'assistant', content: replyText };
      setMessages((prev) => [...prev, botMsg]);
    } catch (e) {
      setMessages((prev) => [
        ...prev,
        { id: crypto.randomUUID(), role: 'assistant', content: 'Sorry, I had trouble responding. Please try again later.' },
      ]);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <button
        onClick={() => setOpen((v) => !v)}
        className="fixed bottom-24 right-6 bg-blue-600 hover:bg-blue-700 text-white p-4 rounded-full shadow-lg transition-all duration-200 hover:scale-110 z-50"
        aria-label={open ? 'Close chatbot' : 'Open chatbot'}
        aria-expanded={open}
      >
        <MessageCircle className="h-6 w-6" />
      </button>

      {open && (
        <div
          role="dialog"
          aria-modal="true"
          className="fixed bottom-6 right-6 w-80 sm:w-96 bg-white rounded-xl shadow-2xl border border-gray-200 z-50 flex flex-col overflow-hidden"
        >
          <div className="flex items-center justify-between px-4 py-3 bg-blue-600 text-white">
            <div className="font-semibold">Academy Assistant</div>
            <button className="p-1" onClick={() => setOpen(false)} aria-label="Close chat">
              <X className="h-5 w-5" />
            </button>
          </div>
          <div className="flex-1 overflow-y-auto p-4 space-y-3">
            {messages.length === 0 && (
              <div className="text-sm text-gray-600">
                Hi! I can help with admissions, courses, and preparation tips.
                Try one of these:
                <div className="mt-2 flex flex-wrap gap-2">
                  {defaultSuggestions.map((s) => (
                    <button
                      key={s}
                      onClick={() => sendMessage(s)}
                      className="text-xs px-2 py-1 rounded-full bg-gray-100 hover:bg-gray-200"
                    >
                      {s}
                    </button>
                  ))}
                </div>
              </div>
            )}
            {messages.map((m) => (
              <div key={m.id} className={m.role === 'user' ? 'text-right' : 'text-left'}>
                <div
                  className={
                    'inline-block px-3 py-2 rounded-lg ' +
                    (m.role === 'user'
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-100 text-gray-800')
                  }
                >
                  {m.content}
                </div>
              </div>
            ))}
            {loading && <div className="text-xs text-gray-500">Assistant is typing…</div>}
            <div ref={endRef} />
          </div>
          <form
            className="p-3 border-t border-gray-200 flex items-center space-x-2"
            onSubmit={(e) => {
              e.preventDefault();
              sendMessage();
            }}
          >
            <input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              className="flex-1 px-3 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Type your message..."
              aria-label="Message"
            />
            <button
              type="submit"
              className="p-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg disabled:opacity-50"
              disabled={loading}
              aria-label="Send message"
            >
              <Send className="h-5 w-5" />
            </button>
          </form>
        </div>
      )}
    </div>
  );
};

export default Chatbot;