import React from 'react';
import { Helmet } from 'react-helmet-async';

type SEOProps = {
  title?: string;
  description?: string;
  canonical?: string;
};

const defaultTitle = 'Elite Defence Academy';
const defaultDescription =
  'Premier defence coaching for NDA, CDS, AFCAT and SSB interviews. Excellence through discipline.';
const defaultCanonical = 'https://AbhijeetRaj646.github.io/defence-academy-website/';

const SEO: React.FC<SEOProps> = ({ title, description, canonical }) => {
  const pageTitle = title ? `${title} | ${defaultTitle}` : defaultTitle;
  const pageDescription = description ?? defaultDescription;
  const pageCanonical = canonical ?? defaultCanonical;

  return (
    <Helmet>
      <title>{pageTitle}</title>
      <meta name="description" content={pageDescription} />
      <link rel="canonical" href={pageCanonical} />
      <meta property="og:title" content={pageTitle} />
      <meta property="og:description" content={pageDescription} />
      <meta property="og:url" content={pageCanonical} />
      <meta name="twitter:title" content={pageTitle} />
      <meta name="twitter:description" content={pageDescription} />
    </Helmet>
  );
};

export default SEO;