import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Menu, X, Shield, Phone, Mail, LogIn, LogOut } from 'lucide-react';
import { useAuth } from '../auth/AuthProvider';

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const location = useLocation();
  const { user, loginWithGoogle, logout } = useAuth();

  const navItems = [
    { path: '/', label: 'Home' },
    { path: '/about', label: 'About Us' },
    { path: '/courses', label: 'Courses & Fees' },
    { path: '/gallery', label: 'Gallery' },
    { path: '/blog', label: 'Blog/News' },
    { path: '/contact', label: 'Contact Us' },
  ];

  const isActive = (path: string) => location.pathname === path;

  return (
    <div>
      {/* Top Bar */}
      <div className="bg-blue-900 text-white text-sm py-2">
        <div className="max-w-7xl mx-auto px-4 flex flex-col md:flex-row justify-between items-center">
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-2">
              <Phone className="h-4 w-4" />
              <span>+91-9876543210</span>
            </div>
            <div className="flex items-center space-x-2">
              <Mail className="h-4 w-4" />
              <span>info@defenceacademy.com</span>
            </div>
          </div>
          <div className="text-center mt-1 md:mt-0">
            <span className="text-yellow-400 font-semibold">Excellence in Defence Training Since 2010</span>
          </div>
        </div>
      </div>

      {/* Main Header */}
      <header className="bg-white shadow-lg sticky top-0 z-40" role="banner">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex items-center justify-between h-20">
            {/* Logo */}
            <Link to="/" className="flex items-center space-x-3" aria-label="Elite Defence Academy Home">
              <Shield className="h-10 w-10 text-blue-900" />
              <div>
                <h1 className="text-2xl font-bold text-blue-900">Elite Defence Academy</h1>
                <p className="text-sm text-gray-600">Excellence Through Discipline</p>
              </div>
            </Link>

            {/* Desktop Navigation */}
            <nav className="hidden lg:flex space-x-8" aria-label="Main navigation">
              {navItems.map((item) => (
                <Link
                  key={item.path}
                  to={item.path}
                  className={`px-3 py-2 rounded-md text-sm font-medium transition-colors duration-200 ${
                    isActive(item.path)
                      ? 'bg-blue-900 text-white'
                      : 'text-gray-700 hover:text-blue-900 hover:bg-blue-50'
                  }`}
                >
                  {item.label}
                </Link>
              ))}
            </nav>

            {/* Auth / CTA */}
            <div className="hidden lg:flex items-center space-x-3">
              {!user ? (
                <button
                  onClick={loginWithGoogle}
                  className="flex items-center space-x-2 border border-blue-900 text-blue-900 hover:bg-blue-900 hover:text-white font-semibold px-4 py-2 rounded-lg transition-colors duration-200"
                  aria-label="Sign in with Google"
                >
                  <LogIn className="h-4 w-4" />
                  <span>Sign In</span>
                </button>
              ) : (
                <>
                  <span className="text-sm text-gray-700">Hi, {user.displayName || 'Student'}</span>
                  <Link
                    to="/dashboard"
                    className="bg-yellow-500 hover:bg-yellow-600 text-blue-900 font-semibold px-4 py-2 rounded-lg transition-colors duration-200"
                  >
                    Dashboard
                  </Link>
                  <button
                    onClick={logout}
                    className="flex items-center space-x-2 text-gray-600 hover:text-blue-900"
                    aria-label="Sign out"
                  >
                    <LogOut className="h-4 w-4" />
                    <span>Sign Out</span>
                  </button>
                </>
              )}
            </div>

            {/* Mobile Menu Button */}
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className="lg:hidden p-2 rounded-md text-gray-700 hover:text-blue-900"
              aria-label="Toggle navigation menu"
              aria-expanded={isMenuOpen}
              aria-controls="mobile-menu"
            >
              {isMenuOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isMenuOpen && (
          <div id="mobile-menu" className="lg:hidden bg-white border-t" role="navigation" aria-label="Mobile navigation">
            <div className="px-2 pt-2 pb-3 space-y-1">
              {navItems.map((item) => (
                <Link
                  key={item.path}
                  to={item.path}
                  onClick={() => setIsMenuOpen(false)}
                  className={`block px-3 py-2 rounded-md text-base font-medium transition-colors duration-200 ${
                    isActive(item.path)
                      ? 'bg-blue-900 text-white'
                      : 'text-gray-700 hover:text-blue-900 hover:bg-blue-50'
                  }`}
                >
                  {item.label}
                </Link>
              ))}
              {!user ? (
                <button
                  onClick={() => {
                    setIsMenuOpen(false);
                    loginWithGoogle();
                  }}
                  className="w-full flex items-center justify-center space-x-2 border border-blue-900 text-blue-900 hover:bg-blue-900 hover:text-white font-semibold px-4 py-2 rounded-lg transition-colors duration-200"
                >
                  <LogIn className="h-4 w-4" />
                  <span>Sign In</span>
                </button>
              ) : (
                <>
                  <Link
                    to="/dashboard"
                    onClick={() => setIsMenuOpen(false)}
                    className="block px-3 py-2 bg-yellow-500 text-blue-900 font-semibold rounded-md text-center"
                  >
                    Dashboard
                  </Link>
                  <button
                    onClick={() => {
                      setIsMenuOpen(false);
                      logout();
                    }}
                    className="w-full flex items-center justify-center space-x-2 text-gray-700 hover:text-blue-900 px-3 py-2"
                  >
                    <LogOut className="h-4 w-4" />
                    <span>Sign Out</span>
                  </button>
                </>
              )}
            </div>
          </div>
        )}
      </header>
    </div>
  );
};

export default Header;